// MODE MENU ITEM
.toggle-container {
  position: absolute;
  margin-top: -14%;
  margin-left: 77%;
}

// MODE MODAL
.modal-body {
  margin-top: 1.25rem;
}

.dropdown-menu {
  text-align: center;

  & > i {
    display: none;
  }

  @media (max-width: 570px) {
    .button-text {
      display: none;
    }
  }
}

.dropdown-menu-mobile {
  text-align: left;

  @media (min-width: 570px) {
    display: none;
  }
}
